<template>
    <view>
        <view class="accordion-header">
            <view class="one-line__bettween">
                <slot name="header" :title="title" />
                <u-switch v-model="isChildExpand" @change="(e) => handleItemCheck(e, index)"></u-switch>
            </view>
        </view>
        <transition name="accordion">
            <view
                :class="{
                    'accordion-content': true,
                    'accordion-content-hidden': !item.isExpand,
                }">
                <slot name="content" :content="content" />
            </view>
        </transition>
    </view>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: '默认标题',
        },
        content: {
            type: String,
            default: '默认标题',
        },
        index: {
            type: Number,
            default: 0,
        },
        isExpand: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            activeIndex: null,
            isChildExpand: this.isExpand,
        }
    },
    methods: {
        toggleItem(index) {
            if (this.activeIndex === index) {
                this.activeIndex = null
            } else {
                this.activeIndex = index
            }
        },
        handleItemCheck(e, index, isExpand) {
            console.log('handleItemCheck', e, index, isExpand)
            this.isChildExpand = e
            if (this.activeIndex === index) {
                this.activeIndex = null
            } else {
                this.activeIndex = index
            }
            this.$emit('change', { e, index })
            // this.payTypeList[index].isExpand = e
        },
    },
}
</script>

